<template>
	<div v-cloak>
		<div class="modal-mask level-5 p-3">
			<div class="donate-modal-wapper lock-mobile-width">
				<div class="container-sm p-2">
					<div class="row flex-center">
						<div class="mt-4 col-12 text-center fs-4"></div>
						<hr class="m-3" />
					</div>
					<div class="row">
						<div class="col-12 flex-center" id="qrcode"></div>
						<div class="col-12 px-3 fs-6 mb-3 mt-2 text-center">
							<div class="ms-1"> <i class="bi bi-arrow-up-circle-fill"></i> QR code  </div>
						</div>
						<div class="col-12">
							<div class="mt-4">
								<div class="ms-1 text-center">
									<button v-show="!isCopy" class="bdr-12 w-50 ht-40 btn-color-red" @click="share()">
										 <i class="bi bi-box-arrow-up"></i>
									</button>
									<button v-show="isCopy" class="bdr-12 w-50 ht-40 btn-color-wine-red" disabled>
										 <i class="bi bi-check-circle-fill"></i> 
									</button>
									<p id="copy" style="display: none;">{{ href }}</p>
								</div>
								<div class="mt-2 fs-6 text-center"> <i class="bi bi-arrow-up-circle-fill"></i>  </div>
							</div>
						</div>
					</div>
					<div class="ht-10"></div>
					<div class="w-100 text-center flex-center my-4" @click="close">
						<button class="bdr-12 w-50 ht-40 btn-color-light-red"></button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "shareModal",
	data: function() {
		return {
			jkospayImg: require("@/assets/img/jkospay.png").default,
			isCopy: false,
			href: window.location.href,
		};
	},
	mounted: function() {
		$("#qrcode").qrcode({
			render: "canvas", //table
			minVersion: 1, // version range somewhere in 1 .. 40
			maxVersion: 40,
			ecLevel: "H", //  'L', 'M', 'Q' or 'H'
			left: 0,
			top: 0,
			size: 200, //
			width: 210,
			height: 210,
			text: window.location.href, //
			radius: 0.1, // 0.0 .. 0.5
			quiet: 2, //
			mode: 4,
			mSize: 0.2, //
			mPosX: 0.5,
			mPosY: 0.5,
			label: "jQuery.qrcode",
			fontname: "sans",
		});
	},
	methods: {
		close: function() {
			this.$parent.isShowShareModal = false;
			this.isCopy = false;
		},
		share: function() {
			if (navigator.share) {
				navigator
					.share({
						title: "" + this.$parent.playerNum + "",
						text: "" + this.$parent.playerNum + "",
						url: window.location.href,
					})
					.then(() => console.log("Successful share"))
					.catch((error) => console.log("Error sharing", error));
			} else {
				window.Clipboard.copy(window.location.href);
				this.isCopy = true;
			}
		},
	},
};
</script>
